body{font-family:"New Century Schoolbook","TeX Gyre Schola",serif;margin:0;padding:0;background-color:#f8f9fa;color:#333;background-image:url("../images/image1.jpg");background-size:cover;background-position:center;background-attachment:fixed}body::before{content:"";position:fixed;top:0;left:0;width:100%;height:100%;background-image:inherit;background-size:inherit;background-position:inherit;background-attachment:inherit;filter:blur(115px);z-index:-1}.header{background-color:#343a40;padding:20px;color:#fdf7f7}.header .navbar{display:flex;justify-content:center}.header .navbar .nav-list{display:flex;list-style:none;gap:20px}.header .navbar .nav-list li a{color:#fbf8f8;text-decoration:none;font-weight:bold;font-size:2rcap;padding:5px 10px;transition:all .3s ease-in-out}.header .navbar .nav-list li a:hover{background-color:rgb(40.5689655172,45.25,49.9310344828);border-radius:5px}.content{padding:40px;max-width:800px;margin:0 auto;background-color:rgba(200,205,214,.85);border-radius:10px}@media(max-width: 768px){.content{padding:20px}}h1{font-size:2.5em;margin-bottom:20px;color:#212529}@media(max-width: 768px){h1{font-size:2em}}h3{font-size:1.5em;margin-bottom:10px;color:#212529}@media(max-width: 768px){h3{font-size:1.3em}}p{font-size:1.3em;line-height:1.6;color:#6383ba}a{color:#1d3cc5;transition:color .3s ease-in-out}a:hover{text-decoration:underline;color:#0056b3}.projects-container{padding:40px;max-width:800px;margin:0 auto}@media(max-width: 768px){.projects-container{padding:20px}}.project-item{background-color:#afb5ca;padding:15px;margin-bottom:20px;box-shadow:0 4px 8px rgba(0,0,0,.1);border-radius:8px;transition:all .3s ease-in-out}.project-item:hover{transform:scale(1.05);box-shadow:0 6px 12px rgba(0,0,0,.15)}.project-item h3{color:#1d3cc5;margin-bottom:10px}.project-item p{color:#333}.project-item a{color:#1d3cc5;font-weight:bold}.project-item a:hover{text-decoration:underline;color:#0056b3}.contact-form input,.contact-form textarea{width:100%;margin-bottom:15px;padding:10px;border:1px solid #ccc;border-radius:5px}.contact-form button{width:100%;padding:10px;background-color:#1d3cc5;color:#fff;border:none;border-radius:5px;cursor:pointer;transition:background-color .3s ease-in-out}.contact-form button:hover{background-color:#0056b3}[data-aos]{opacity:0;transition:opacity .5s ease-in-out}[data-aos].aos-animate{opacity:1}.contact-container{display:flex;justify-content:space-between;padding:40px;background-color:rgba(199,199,214,.85);border-radius:10px;box-shadow:0 4px 12px rgba(0,0,0,.1)}.contact-container .contact-form,.contact-container .contact-info{flex:1;padding:20px;margin:0 10px}.contact-container h1,.contact-container h2{color:#1d3cc5;text-align:center;font-size:2.2em;margin-bottom:20px}.contact-container .contact-form form{display:flex;flex-direction:column}.contact-container .contact-form form label{font-size:1.1em;color:#333;margin-bottom:5px}.contact-container .contact-form form input,.contact-container .contact-form form textarea{width:100%;padding:10px;margin-bottom:15px;border-radius:5px;border:1px solid #6383ba;font-size:1em;background-color:hsla(0,0%,100%,.9)}.contact-container .contact-form form button{padding:12px;background-color:#1d3cc5;color:#fff;border:none;border-radius:5px;font-size:1.2em;cursor:pointer;transition:background-color .3s ease-in-out}.contact-container .contact-form form button:hover{background-color:#0056b3}.contact-container .contact-info ul{list-style:none;padding:0;font-size:1.1em}.contact-container .contact-info ul li{margin-bottom:15px;color:#333;position:relative}.contact-container .contact-info ul li:before{content:"•";color:#1d3cc5;font-size:1.5em;position:absolute;left:-25px;top:0}.contact-container .contact-info ul li a{color:#1d3cc5;text-decoration:none;font-weight:bold;transition:color .3s ease-in-out}.contact-container .contact-info ul li a:hover{color:#1d4ed8;text-decoration:underline}.footer{background-color:#343a40;color:#fff;text-align:center;padding:10px;position:relative;width:100%;bottom:0}.about-container .profile-image{border-radius:10px;width:180px;height:auto;object-fit:cover;margin-bottom:20px;box-shadow:0 4px 10px rgba(0,0,0,.1)}.about-container{background-color:rgba(117,125,136,.95);padding:40px;border-radius:15px;box-shadow:0 4px 12px rgba(0,0,0,.1);margin:35px auto;max-width:900px;text-align:center}.about-container .title{font-size:2.5em;margin-bottom:20px;color:#101f65;text-transform:uppercase;letter-spacing:2px}.about-container .subtitle{font-size:1.7em;margin-top:30px;color:#1c3d5e;text-transform:uppercase;letter-spacing:1px}.about-container .about-text,.about-container .experience-text,.about-container .skills-text{font-size:1.3rem;color:#242431;line-height:1.7;margin-bottom:20px}.about-container .skills-list{list-style-type:none;padding:0;font-size:1.5rem}.about-container .skills-list li{font-size:1.1rem;color:#09163d;margin-bottom:10px;padding-left:0;position:relative}.about-container .skills-list li:before{content:"•";position:absolute;left:0;color:#0d132e;font-size:1.5rem;margin-right:10px}.certificates-container ul{list-style:none;padding:0}.certificates-container ul li{margin-bottom:20px}.certificates-container ul li a{color:#27296f;font-weight:bold;text-decoration:none;font-size:1.5rem}.certificates-container ul li p{color:#2b2c2e;font-size:1.2rem}.certificates-container ul li a:hover{text-decoration:underline}